.stepper,
.unit--desc-i {
  position: relative; }

.stepper img {
  position: absolute;
  right: -2px;
  top: 0;
  z-index: 100;
  pointer-events: none; }

.unit--desc-i img {
  position: absolute;
  top: 0;
  right: 0; }

@media (max-width: 499px) {
  body,
  .container,
  .sub_category_page .column_right_products_container,
  .product_horizontal {
    width: 100%;
    min-width: 100%; }
  .sub_category_page .column_right_products_container {
    padding: 0 15px; }
  .product_horizontal .product_photo {
    margin: 10px auto; } }
